.ag-format-container {
  width: 1181px;
  margin: 0 auto;
}

.ag-title {
  padding: 0 0 30px;
  text-align: center;
  font-size: 30px;
}

.ag-block {
  margin: 0 0 50px;
  padding: 40px;
  border: 1px solid #ccc;
}

@media only screen and (max-width: 767px) {
  .ag-format-container {
    width: 90%;
  }
}

@media only screen and (max-width: 639px) {
  .ag-format-container {
    width: 86%;
  }
}

@media (min-width: 768px) and (max-width: 980px) {
  .ag-format-container {
    width: 748px;
  }
}

@media (min-width: 979px) and (max-width: 1180px) {
  .ag-format-container {
    width: 960px;
  }
}

@media (any-hover: hover) {
  a:not(.ag-base_btn):hover {
    text-decoration: underline;
  }
}